/* Button
   ========================================================================== */
/*
** 1. Change the font styles in all browsers.
** 2. Remove the margin in Firefox and Safari.
*/
.button-reset {
  font-family: inherit;
  font-size: 100%;
  line-height: 1;
  margin: 0; }

/*
** Show the overflow in IE.
** 1. Show the overflow in Edge.
*/
.button-reset {
  overflow: visible; }

/*
** Remove the inheritance of text transform in Edge, Firefox, and IE.
** 1. Remove the inheritance of text transform in Firefox.
*/
.button-reset {
  text-transform: none; }

/*
** Correct the inability to style clickable types in iOS and Safari.
*/
.reset-button {
  -webkit-appearance: button; }

/*
** Remove the inner border and padding in Firefox.
**/
.button-reset::-moz-focus-inner {
  border-style: none;
  padding: 0; }

/*
** Restore the focus styles unset by the previous rule.
*/
.button-reset:-moz-focusring {
  outline: 1px dotted ButtonText; }

/*
** Remove default styling
*/
.button-reset {
  background-color: transparent;
  border: none;
  padding: 0; }

.button-reset:focus {
  outline: 0; }

/*
** Cursor.
*/
@media (max-width: 679px) {
  .button-reset {
    cursor: default; } }

@media (min-width: 680px) {
  .button-reset {
    cursor: pointer; } }

/* Link
   ========================================================================== */
/*
** Remove the gray background on active links in IE 10.
*/
.link-reset, .footer__link {
  background-color: transparent; }

/* Image
   ========================================================================== */
/**
 * Remove the border on images inside links in IE 10.
 */
.img-reset, .footer__image {
  border-style: none; }

.footer {
  background-color: #4d97cb;
  color: #faf4eb;
  font-family: Architects Daughter, cursive;
  font-size: 1.1em;
  padding: 40px 40px 30px 40px; }

.footer small {
  display: block; }

.footer__copyright {
  margin-top: 15px;
  text-align: center; }

.footer__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }

.footer__inner > div {
  align-items: center;
  display: flex;
  flex-direction: column; }

.footer__inner > div:not(:last-child) {
  margin-right: 20px; }

.footer__link {
  display: block; }

.footer__link + div {
  border-bottom: 2px solid transparent; }

.footer__link:focus + div,
.footer__link:hover + div {
  border-bottom: 2px solid #98BCBF; }

@media (max-width: 449px) {
  .footer__inner {
    flex-direction: column; }
  .footer__inner > div:not(:last-child) {
    margin-bottom: 10px;
    margin-right: 0; } }
