html {
  -webkit-text-size-adjust: 100%; }

body {
  background-color: #fff;
  color: #2f2e33;
  font-display: block;
  font-family: Poppins, sans-serif;
  margin: 0; }

a,
button,
input,
select,
textarea {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

@page {
  margin-bottom: 2cm;
  margin-top: 2cm; }
