/* Button
   ========================================================================== */
/*
** 1. Change the font styles in all browsers.
** 2. Remove the margin in Firefox and Safari.
*/
.button-reset, .timeline__content-expander-button, .timeline__content-item-year {
  font-family: inherit;
  font-size: 100%;
  line-height: 1;
  margin: 0; }

/*
** Show the overflow in IE.
** 1. Show the overflow in Edge.
*/
.button-reset, .timeline__content-expander-button, .timeline__content-item-year {
  overflow: visible; }

/*
** Remove the inheritance of text transform in Edge, Firefox, and IE.
** 1. Remove the inheritance of text transform in Firefox.
*/
.button-reset, .timeline__content-expander-button, .timeline__content-item-year {
  text-transform: none; }

/*
** Correct the inability to style clickable types in iOS and Safari.
*/
.reset-button {
  -webkit-appearance: button; }

/*
** Remove the inner border and padding in Firefox.
**/
.button-reset::-moz-focus-inner, .timeline__content-expander-button::-moz-focus-inner, .timeline__content-item-year::-moz-focus-inner {
  border-style: none;
  padding: 0; }

/*
** Restore the focus styles unset by the previous rule.
*/
.button-reset:-moz-focusring, .timeline__content-expander-button:-moz-focusring, .timeline__content-item-year:-moz-focusring {
  outline: 1px dotted ButtonText; }

/*
** Remove default styling
*/
.button-reset, .timeline__content-expander-button, .timeline__content-item-year {
  background-color: transparent;
  border: none;
  padding: 0; }

.button-reset:focus, .timeline__content-expander-button:focus, .timeline__content-item-year:focus {
  outline: 0; }

/*
** Cursor.
*/
@media (max-width: 679px) {
  .button-reset, .timeline__content-expander-button, .timeline__content-item-year {
    cursor: default; } }

@media (min-width: 680px) {
  .button-reset, .timeline__content-expander-button, .timeline__content-item-year {
    cursor: pointer; } }

/* Link
   ========================================================================== */
/*
** Remove the gray background on active links in IE 10.
*/
.link-reset, .timeline__content-item-img > a {
  background-color: transparent; }

/* Image
   ========================================================================== */
/**
 * Remove the border on images inside links in IE 10.
 */
.img-reset, .timeline__content-item-img > a > img {
  border-style: none; }

.timeline__content-expander {
  position: absolute;
  text-align: center;
  top: 2px; }

.timeline__content-expander-button {
  background-color: transparent;
  border-radius: 6px;
  box-shadow: none;
  color: #9E9E9E;
  height: 35px;
  transition: all 0.5s;
  width: 50px; }

.timeline__content-item {
  position: relative; }

.timeline__content-item-details {
  font-size: 0.9em; }

.timeline__content-item-details > h5 {
  font-size: 1.2em;
  margin: 0;
  text-align: left; }

.timeline__content-item-details > p {
  margin: 0;
  text-align: justify; }

.timeline__content-item-end::before {
  background-color: #337bae;
  border-radius: 50%;
  content: '';
  display: inline-block;
  height: 15px;
  margin-left: 3px;
  margin-right: 8px;
  vertical-align: -3px;
  width: 15px; }

.timeline__content-item-inner {
  align-items: center;
  display: flex;
  justify-content: center;
  margin-left: 28px;
  margin-right: 15px;
  overflow: hidden; }

.timeline__content-item-img > a > img {
  max-height: 180px;
  width: 180px; }

.timeline__content-item-year {
  align-items: center;
  display: inline-flex;
  color: #337bae;
  font-size: 1em;
  font-weight: 500;
  margin-right: auto;
  z-index: 5; }

.timeline__content-item-year::before {
  background-color: #337bae;
  border-radius: 50%;
  content: '';
  display: inline-block;
  height: 19px;
  margin-left: 1px;
  margin-right: 8px;
  width: 19px; }

.timeline__content-items {
  position: relative; }

.timeline__content-items::before {
  border-left: 5px solid #337bae;
  content: '';
  display: block;
  height: calc(100% - 20px);
  left: 8px;
  pointer-events: none;
  position: absolute;
  top: 5px; }

.timeline__content-items .timeline__content-item:not(:last-child) {
  margin-bottom: 20px; }

@media (min-width: 700px) {
  .timeline__content-expander {
    left: calc(50% - 28px); }
  .timeline__content-items {
    margin-left: 20px; }
  .timeline__content-item-details {
    flex-grow: 1;
    max-width: 700px; }
  .timeline__content-item-inner {
    flex-wrap: nowrap; }
  .timeline__content-item-img > a > img {
    margin-left: 30px; }
  .timeline__content-item-img {
    margin-top: 20px; } }

@media (max-width: 699px) {
  .timeline__content-expander {
    left: calc(50% - 18px); }
  .timeline__content-items {
    margin-left: 0; }
  .timeline__content-item-details {
    flex-grow: 1;
    max-width: 500px; }
  .timeline__content-item-inner {
    flex-wrap: wrap-reverse; }
  .timeline__content-item-img > a > img {
    margin: 5px 0; }
  .timeline__content-item-img {
    margin-bottom: 15px;
    margin-top: 0; } }

@media (max-width: 449px) {
  .timeline__content-item-img > a > img {
    display: block;
    height: 100%;
    width: auto; }
  .timeline__content-item-img {
    height: 100px;
    margin-bottom: 15px;
    margin-top: 15px; } }

@media (min-width: 500px) {
  .timeline__content-expander {
    display: block;
    position: absolute; }
  .timeline__content-expander-button {
    padding: 5px 10px; }
  .timeline__content-expander-button:focus,
  .timeline__content-expander-button:hover {
    background-color: #E0E0E0;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1); }
  .timeline__content-item-year:focus,
  .timeline__content-item-year:hover {
    text-decoration: underline; } }

@media (max-width: 499px) {
  .timeline__content-expander {
    display: inline;
    left: 0;
    position: relative;
    top: -3px; }
  .timeline__content-expander-button {
    padding: 0 2px; }
  .timeline__content-item-details > p {
    text-align: left; } }
