.contact {
  background: linear-gradient(180deg, #98BCBF, #faf4eb);
  display: flex;
  height: 400px;
  position: relative;
  width: 100vw; }

.contact__about {
  align-items: center;
  display: inline-flex;
  flex-direction: column;
  flex-grow: 1;
  height: 400px;
  position: absolute;
  width: 100%; }

.contact__about a {
  color: #337bae;
  font-family: Poppins, sans-serif;
  font-size: 2em;
  font-weight: 500px;
  position: relative;
  text-decoration: none;
  top: 60px; }

.contact__about a:focus,
.contact__about a:hover {
  color: #337bae;
  outline: none;
  text-decoration: underline; }

.contact__about h3 {
  font-family: Lalezar, cursive;
  font-size: 3em;
  left: -10%;
  position: relative;
  top: 100px; }

.contact__banner-grass {
  background: url("./assets/banner-grass.svg") bottom right repeat-x;
  background-size: contain;
  bottom: 0;
  height: 7.5px;
  opacity: 0.85;
  position: absolute;
  right: 0;
  width: 380px; }

/* Banner images */
@media (max-width: 679px) {
  .contact__banner-grass {
    left: 302px;
    position: absolute;
    right: auto; } }

/* Banner title */
@media (max-width: 869px) {
  .contact__about a {
    bottom: 140px;
    left: 200px;
    position: absolute;
    top: auto; }
  .contact__about h3 {
    font-size: 2.5em;
    left: 150px;
    position: absolute;
    top: 50px; } }

@media (max-width: 599px) {
  .contact__about > a {
    font-size: 1.7em; }
  .contact__about > h3 {
    font-size: 2.5em; } }

@media (max-width: 449px) {
  .contact__about > a {
    left: 150px; }
  .contact__about > h3 {
    left: 125px; } }

@media (max-width: 374px) {
  .contact__about > a {
    font-size: 1.5em;
    left: 100px; }
  .contact__about > h3 {
    font-size: 2.2em;
    left: 75px; } }

@media print {
  .contact {
    background-color: unset;
    color: black;
    font-size: 12px; } }

@media screen {
  .contact {
    background-color: #faf4eb; } }
