.cv {
  align-items: center;
  background-color: #faf4eb;
  background-image: url("../assets/png/wine-cork.png");
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - 30px);
  padding: 10px 20px 20px 20px; }

.cv__inner {
  font-family: Poppins, sans-serif;
  max-width: 950px;
  width: 100%; }

.cv__inner > div:not(:last-child) {
  margin-bottom: 20px; }

.cv__links {
  display: flex;
  flex-wrap: wrap;
  font-family: Lalezar, cursive;
  font-size: 1.3em;
  list-style: none;
  justify-content: center;
  padding-left: 0;
  text-transform: uppercase; }

.cv__links li {
  margin: 10px; }

.cv header > h1 {
  font-size: 1.5em;
  margin: 0; }

.cv section {
  margin-top: 20px; }

.cv section > h2 {
  border-bottom: 2px solid #1a405f;
  font-family: Lalezar, cursive;
  font-size: 1.2em;
  margin-bottom: 10px;
  padding-left: 2px;
  text-transform: uppercase; }

@media print {
  .cv {
    background-color: unset;
    color: black;
    font-size: 12px; }
  .cv__inner {
    margin-top: 0; }
  .cv a {
    color: black;
    display: inline;
    text-decoration: none; }
  .cv__email:after {
    content: ": knightjdr@gmail"; } }

@media screen {
  .cv {
    background-color: #faf4eb; }
  .cv__inner {
    margin-top: 50px; }
  .cv__inner a:link,
  .cv__inner a:visited {
    color: #337bae;
    text-decoration: none; }
  .cv__inner a:active,
  .cv__inner a:focus,
  .cv__inner a:hover {
    outline: 0;
    text-decoration: underline; } }
