/* Button
   ========================================================================== */
/*
** 1. Change the font styles in all browsers.
** 2. Remove the margin in Firefox and Safari.
*/
.button-reset, .missing button {
  font-family: inherit;
  font-size: 100%;
  line-height: 1;
  margin: 0; }

/*
** Show the overflow in IE.
** 1. Show the overflow in Edge.
*/
.button-reset, .missing button {
  overflow: visible; }

/*
** Remove the inheritance of text transform in Edge, Firefox, and IE.
** 1. Remove the inheritance of text transform in Firefox.
*/
.button-reset, .missing button {
  text-transform: none; }

/*
** Correct the inability to style clickable types in iOS and Safari.
*/
.reset-button {
  -webkit-appearance: button; }

/*
** Remove the inner border and padding in Firefox.
**/
.button-reset::-moz-focus-inner, .missing button::-moz-focus-inner {
  border-style: none;
  padding: 0; }

/*
** Restore the focus styles unset by the previous rule.
*/
.button-reset:-moz-focusring, .missing button:-moz-focusring {
  outline: 1px dotted ButtonText; }

/*
** Remove default styling
*/
.button-reset, .missing button {
  background-color: transparent;
  border: none;
  padding: 0; }

.button-reset:focus, .missing button:focus {
  outline: 0; }

/*
** Cursor.
*/
@media (max-width: 679px) {
  .button-reset, .missing button {
    cursor: default; } }

@media (min-width: 680px) {
  .button-reset, .missing button {
    cursor: pointer; } }

/* Link
   ========================================================================== */
/*
** Remove the gray background on active links in IE 10.
*/
.link-reset, .missing a {
  background-color: transparent; }

/* Image
   ========================================================================== */
/**
 * Remove the border on images inside links in IE 10.
 */
.img-reset, .missing img {
  border-style: none; }

.missing {
  align-items: center;
  background-color: #1a405f;
  background-image: url("../assets/png/bright-squares.png");
  color: #faf4eb;
  display: flex;
  flex-direction: column;
  height: calc(100vh - 80px);
  justify-content: center;
  min-height: 300px;
  padding: 40px;
  width: calc(100vw - 80px); }

.missing button {
  background-color: #4d97cb;
  border-radius: 4px;
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.1);
  color: #faf4eb;
  font-size: 1.3em;
  height: auto;
  padding: 8px 10px;
  transition: background-color 0.4s;
  width: auto; }

.missing button:focus,
.missing button:hover {
  background-color: #337bae;
  outline: 0; }

.missing a:focus {
  outline: 0; }

.missing img {
  height: 35%;
  margin-bottom: 30px; }

.missing p {
  font-size: 1.5em;
  margin-bottom: 0 0 20px 0;
  max-width: 600px;
  text-align: center; }
