/* Button
   ========================================================================== */
/*
** 1. Change the font styles in all browsers.
** 2. Remove the margin in Firefox and Safari.
*/
.button-reset {
  font-family: inherit;
  font-size: 100%;
  line-height: 1;
  margin: 0; }

/*
** Show the overflow in IE.
** 1. Show the overflow in Edge.
*/
.button-reset {
  overflow: visible; }

/*
** Remove the inheritance of text transform in Edge, Firefox, and IE.
** 1. Remove the inheritance of text transform in Firefox.
*/
.button-reset {
  text-transform: none; }

/*
** Correct the inability to style clickable types in iOS and Safari.
*/
.reset-button {
  -webkit-appearance: button; }

/*
** Remove the inner border and padding in Firefox.
**/
.button-reset::-moz-focus-inner {
  border-style: none;
  padding: 0; }

/*
** Restore the focus styles unset by the previous rule.
*/
.button-reset:-moz-focusring {
  outline: 1px dotted ButtonText; }

/*
** Remove default styling
*/
.button-reset {
  background-color: transparent;
  border: none;
  padding: 0; }

.button-reset:focus {
  outline: 0; }

/*
** Cursor.
*/
@media (max-width: 679px) {
  .button-reset {
    cursor: default; } }

@media (min-width: 680px) {
  .button-reset {
    cursor: pointer; } }

/* Link
   ========================================================================== */
/*
** Remove the gray background on active links in IE 10.
*/
.link-reset, .projects__grid-description-inner a {
  background-color: transparent; }

/* Image
   ========================================================================== */
/**
 * Remove the border on images inside links in IE 10.
 */
.img-reset, .projects__image {
  border-style: none; }

.projects {
  background-color: #1a405f;
  color: #faf4eb;
  font-family: Architects Daughter, cursive;
  font-size: 1.1em;
  padding: 60px 40px 80px 40px; }

.projects__desktop {
  display: block; }

.projects__grid {
  display: flex;
  flex-direction: column;
  margin-top: 15px;
  max-width: 1000px;
  width: 100%; }

.projects__grid > div:not(:last-child) {
  border-bottom: 1px solid rgba(248, 241, 229, 0.2);
  margin-bottom: 40px;
  padding-bottom: 40px; }

.projects__grid-description {
  align-items: center;
  display: inline-flex;
  max-width: 750px; }

.projects__grid-description-inner {
  font-family: Architects Daughter, cursive;
  font-size: 1.1em;
  text-align: justify; }

.projects__grid-description-inner a {
  color: #f1f10e;
  font-family: Poppins, sans-serif;
  font-size: 0.85em;
  text-decoration: none; }

.projects__grid-description-inner a:focus,
.projects__grid-description-inner a:hover {
  color: #65ffc8;
  outline: 0;
  text-decoration: underline; }

.projects__grid-description-inner p {
  margin: 0; }

.projects__grid-image {
  align-self: center;
  display: flex;
  height: 100%;
  justify-content: center;
  padding: 0 20px;
  width: 250px; }

.projects__grid-row {
  display: inline-flex;
  justify-content: center; }

.projects__image {
  height: 100%; }

.projects__inner {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px; }

.project__link-period::after {
  color: #faf4eb;
  content: '.'; }

.projects__inner h3 {
  font-family: Poppins, sans-serif;
  font-size: 1.3em;
  font-weight: bold;
  margin-bottom: 40px;
  position: relative; }

.projects__inner h3:before {
  content: '';
  position: absolute;
  left: 12.5%;
  bottom: 0;
  height: 2px;
  width: 75%;
  border-bottom: 2px solid #faf4eb; }

.projects__mobile {
  display: none; }

@media (max-width: 749px) {
  .projects__desktop {
    display: none; }
  .projects__mobile {
    display: block; } }

@media (max-width: 649px) {
  .projects__grid-row {
    flex-direction: column; }
  .projects__grid > div:nth-child(even) {
    flex-direction: column-reverse; }
  .projects__grid-description {
    justify-content: center; }
  .projects__grid-image {
    margin-bottom: 20px; } }
