.no-script {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-family: Poppins, sans-serif;
  padding: 0 20px; }

.no-script a {
  color: #1a405f;
  display: block;
  font-weight: bold;
  text-decoration: none; }

.no-script a:focus,
.no-script a:hover {
  color: #337bae;
  text-decoration: underline; }

.no-script h1 {
  font-size: 2em; }

.no-script section {
  max-width: 900px;
  text-align: justify; }
