/* Button
   ========================================================================== */
/*
** 1. Change the font styles in all browsers.
** 2. Remove the margin in Firefox and Safari.
*/
.button-reset, .navbar__menu-backdrop, .navbar__menu-button {
  font-family: inherit;
  font-size: 100%;
  line-height: 1;
  margin: 0; }

/*
** Show the overflow in IE.
** 1. Show the overflow in Edge.
*/
.button-reset, .navbar__menu-backdrop, .navbar__menu-button {
  overflow: visible; }

/*
** Remove the inheritance of text transform in Edge, Firefox, and IE.
** 1. Remove the inheritance of text transform in Firefox.
*/
.button-reset, .navbar__menu-backdrop, .navbar__menu-button {
  text-transform: none; }

/*
** Correct the inability to style clickable types in iOS and Safari.
*/
.reset-button {
  -webkit-appearance: button; }

/*
** Remove the inner border and padding in Firefox.
**/
.button-reset::-moz-focus-inner, .navbar__menu-backdrop::-moz-focus-inner, .navbar__menu-button::-moz-focus-inner {
  border-style: none;
  padding: 0; }

/*
** Restore the focus styles unset by the previous rule.
*/
.button-reset:-moz-focusring, .navbar__menu-backdrop:-moz-focusring, .navbar__menu-button:-moz-focusring {
  outline: 1px dotted ButtonText; }

/*
** Remove default styling
*/
.button-reset, .navbar__menu-backdrop, .navbar__menu-button {
  background-color: transparent;
  border: none;
  padding: 0; }

.button-reset:focus, .navbar__menu-backdrop:focus, .navbar__menu-button:focus {
  outline: 0; }

/*
** Cursor.
*/
@media (max-width: 679px) {
  .button-reset, .navbar__menu-backdrop, .navbar__menu-button {
    cursor: default; } }

@media (min-width: 680px) {
  .button-reset, .navbar__menu-backdrop, .navbar__menu-button {
    cursor: pointer; } }

/* Link
   ========================================================================== */
/*
** Remove the gray background on active links in IE 10.
*/
.link-reset, .navbar__menu-link {
  background-color: transparent; }

/* Image
   ========================================================================== */
/**
 * Remove the border on images inside links in IE 10.
 */
.img-reset {
  border-style: none; }

.navbar__menu {
  cursor: pointer;
  margin-right: 5px;
  margin-top: 5px; }

.navbar__menu-backdrop {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  cursor: default;
  height: 100vh;
  left: 0;
  top: 0;
  position: fixed;
  width: 100vw;
  z-index: 10; }

.navbar__menu-bars {
  transition: fill 0.3s linear; }

.navbar__menu-button:focus .navbar__menu-bars,
.navbar__menu-button:hover .navbar__menu-bars {
  fill: #f1f10e; }

.navbar__menu-inner {
  background-color: #faf4eb;
  border-radius: 4px;
  box-shadow: -3px 3px 5px 0px rgba(0, 0, 0, 0.2);
  opacity: 0;
  position: absolute;
  right: 15px;
  top: 8px;
  transition: all 0.2s linear;
  transform-origin: top right;
  z-index: 11; }

.navbar__menu-inner {
  font-size: 1.5em;
  font-weight: 500;
  list-style: none;
  margin: 0;
  padding: 0; }

.navbar__menu-inner li {
  color: #2f2e33;
  padding: 5px 10px;
  transition: color 0.3s linear; }

.navbar__menu-inner li:hover {
  color: #4d97cb; }

.navbar__menu-inner_hide {
  opacity: 0;
  transform: scale(0); }

.navbar__menu-inner_show {
  opacity: 1;
  transform: scale(1); }

.navbar__menu-link {
  text-decoration: none; }

.navbar_theme-dark .navbar__menu-bars {
  fill: #4d97cb; }

.navbar_theme-transparent .navbar__menu-bars {
  fill: #1a405f; }
