.about {
  background-color: #faf4eb;
  font-family: Architects Daughter, cursive;
  font-size: 1.1em;
  padding: 60px 40px 60px 40px; }

.about__details {
  margin: 0;
  max-width: 1000px;
  text-align: justify; }

.about__inner {
  align-items: center;
  display: flex;
  flex-direction: column; }

.about h3 {
  font-family: Poppins, sans-serif;
  font-size: 1.3em;
  font-weight: bold;
  margin-top: 0;
  margin-bottom: 40px;
  position: relative; }

.about h3:before {
  content: '';
  position: absolute;
  left: 12.5%;
  bottom: 0;
  height: 2px;
  width: 75%;
  border-bottom: 2px solid #2f2e33; }
