/* Button
   ========================================================================== */
/*
** 1. Change the font styles in all browsers.
** 2. Remove the margin in Firefox and Safari.
*/
.button-reset, .timeline__button {
  font-family: inherit;
  font-size: 100%;
  line-height: 1;
  margin: 0; }

/*
** Show the overflow in IE.
** 1. Show the overflow in Edge.
*/
.button-reset, .timeline__button {
  overflow: visible; }

/*
** Remove the inheritance of text transform in Edge, Firefox, and IE.
** 1. Remove the inheritance of text transform in Firefox.
*/
.button-reset, .timeline__button {
  text-transform: none; }

/*
** Correct the inability to style clickable types in iOS and Safari.
*/
.reset-button {
  -webkit-appearance: button; }

/*
** Remove the inner border and padding in Firefox.
**/
.button-reset::-moz-focus-inner, .timeline__button::-moz-focus-inner {
  border-style: none;
  padding: 0; }

/*
** Restore the focus styles unset by the previous rule.
*/
.button-reset:-moz-focusring, .timeline__button:-moz-focusring {
  outline: 1px dotted ButtonText; }

/*
** Remove default styling
*/
.button-reset, .timeline__button {
  background-color: transparent;
  border: none;
  padding: 0; }

.button-reset:focus, .timeline__button:focus {
  outline: 0; }

/*
** Cursor.
*/
@media (max-width: 679px) {
  .button-reset, .timeline__button {
    cursor: default; } }

@media (min-width: 680px) {
  .button-reset, .timeline__button {
    cursor: pointer; } }

/* Link
   ========================================================================== */
/*
** Remove the gray background on active links in IE 10.
*/
.link-reset {
  background-color: transparent; }

/* Image
   ========================================================================== */
/**
 * Remove the border on images inside links in IE 10.
 */
.img-reset {
  border-style: none; }

.timeline__button {
  background-color: #4d97cb;
  border-radius: 50%;
  color: transparent;
  height: 50px;
  position: absolute;
  top: 5px;
  transform: scale(1);
  transition: background 0.3s, transform 0.3s;
  width: 50px; }

.timeline__button::after {
  background-repeat: no-repeat;
  content: " ";
  display: inline-block;
  height: 30px;
  position: relative;
  top: 2px;
  width: 30px; }

.timeline__button-bg {
  background: linear-gradient(to right, #4d97cb 0, #4d97cb 61px, #1a405f 61px, #1a405f 122px, #4d97cb 122px, #4d97cb 183px);
  background-size: 183px 100%;
  border-radius: 50%;
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.1);
  display: inline-block;
  height: 60px;
  position: absolute;
  top: 0;
  transition-duration: 0.3s;
  transition-property: background;
  transition-timing-function: linear;
  width: 60px; }

.timeline__button-career {
  left: 0;
  margin-left: auto;
  margin-right: auto;
  right: 0; }

.timeline__button-career::after {
  background-image: url("./assets/briefcase.svg");
  height: 34px;
  left: 50%;
  margin-left: -17px;
  position: absolute;
  top: 6px;
  width: 34px; }

.timeline__button-career-bg {
  left: 0;
  margin-left: auto;
  margin-right: auto;
  right: 0; }

.timeline__button-education {
  left: 5px; }

.timeline__button-education::after {
  background-image: url("./assets/school.svg");
  height: 34px;
  left: 50%;
  margin-left: -17px;
  position: absolute;
  top: 8px;
  width: 34px; }

.timeline__button-education-bg {
  left: 0; }

.timeline__button-publication {
  right: 5px; }

.timeline__button-publication::after {
  background-image: url("./assets/book.svg");
  height: 32px;
  left: 50%;
  margin-left: -15px;
  position: absolute;
  top: 8px;
  width: 32px; }

.timeline__button-publication-bg {
  right: 0; }

.timeline__button_transition-center {
  background-position: 122px bottom; }

.timeline__button_transition-end {
  transition-delay: 0.45s; }

.timeline__button_transition-inter {
  transition-delay: 0.225s; }

.timeline__button_transition-left {
  background-position: 61px bottom; }

.timeline__button_transition-right {
  background-position: 183px bottom; }

.timeline__button_transition-start {
  transition-delay: 0s; }

.timeline__buttons {
  height: 60px;
  margin-bottom: 5px;
  position: relative; }

.timeline__connector {
  height: 6px;
  position: absolute;
  top: 25px;
  transition-duration: 0.6s;
  transition-property: background;
  transition-timing-function: linear; }

.timeline__connector-left {
  left: 60px; }

.timeline__connector_transition-end {
  transition-delay: 0.3s; }

.timeline__connector_transition-start {
  transition-delay: 0s; }

@media (min-width: 800px) {
  .timeline__buttons {
    min-width: 600px; }
  .timeline__connector {
    background: linear-gradient(to right, rgba(0, 0, 0, 0) 0px, rgba(0, 0, 0, 0) 212px, #1a405f 212px, #1a405f 424px, rgba(0, 0, 0, 0) 424px, rgba(0, 0, 0, 0) 636px);
    background-size: 636px 100%;
    width: 210px; }
  .timeline__connector-right {
    left: 330px; }
  .timeline__connector_transition-left {
    background-position: 636px bottom; }
  .timeline__connector_transition-right {
    background-position: 212px bottom; } }

@media (min-width: 500px) and (max-width: 799px) {
  .timeline__buttons {
    min-width: 400px; }
  .timeline__connector {
    background: linear-gradient(to right, rgba(0, 0, 0, 0) 0px, rgba(0, 0, 0, 0) 112px, #1a405f 112px, #1a405f 224px, rgba(0, 0, 0, 0) 224px, rgba(0, 0, 0, 0) 336px);
    background-size: 336px 100%;
    width: 110px; }
  .timeline__connector-right {
    left: 230px; }
  .timeline__connector_transition-left {
    background-position: 336px bottom; }
  .timeline__connector_transition-right {
    background-position: 112px bottom; } }

@media (max-width: 499px) {
  .timeline__buttons {
    min-width: 280px; }
  .timeline__connector {
    background: linear-gradient(to right, rgba(0, 0, 0, 0) 0px, rgba(0, 0, 0, 0) 52px, #1a405f 52px, #1a405f 104px, rgba(0, 0, 0, 0) 104px, rgba(0, 0, 0, 0) 156px);
    background-size: 156px 100%;
    width: 50px; }
  .timeline__connector-right {
    left: 170px; }
  .timeline__connector_transition-left {
    background-position: 156px bottom; }
  .timeline__connector_transition-right {
    background-position: 52px bottom; } }

@media (min-width: 680px) {
  .timeline__button:focus,
  .timeline__button:hover {
    background: #1a405f;
    transform: scale(1.2); } }
